html, body {
  margin: 0;
  padding: 0;
}

:root {
  --blue: #0a84ff;
  --link-height: 64px;
  --x: 32px;
  --text: #1f1f1f;
}

.header {
  padding: 0 var(--x);
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  align-items: center;
  text-decoration: none;
  display: flex;
}

.logo img {
  width: auto;
  height: 40px;
  display: block;
}

.nav__list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item + .nav__item {
  margin-left: 32px;
}

.nav__link {
  height: var(--link-height);
  line-height: var(--link-height);
  text-transform: uppercase;
  color: var(--text);
  align-items: center;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover, .nav__link.is-active {
  color: var(--blue);
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--blue);
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
