html, body {
  margin: 0;
  padding: 0;
}

:root {
  --blue: #0a84ff;
  --link-height: 64px;  
  --x: 32px;            
  --text: #1f1f1f;
}

.header {
  display: flex;
  align-items: center;     
  justify-content: space-between;
  padding: 0 var(--x);
}

.logo {
  display: flex;
  align-items: center;
  text-decoration: none;
}

.logo img {
  display: block;
  height: 40px;  
  width: auto;
}

.nav__list {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
}

.nav__item + .nav__item {
  margin-left: 32px; 
}

.nav__link {
  display: flex;
  align-items: center;          
  height: var(--link-height);  
  line-height: var(--link-height);
  text-decoration: none;
  text-transform: uppercase;
  color: var(--text);
  position: relative;           
}

.nav__link:hover {
  color: var(--blue);
}

.nav__link.is-active {
  color: var(--blue);
}

.nav__link.is-active::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 2px;              
  background-color: var(--blue);
}
